import store from 'store'

function setUserInfo(token, userInfo) {
    store.set('jwt_token', token)
    store.set('user_info', userInfo)
}

function clearStore() {
    store.clearAll()
}

function getToken() {
    return 'Token ' + store.get('jwt_token');
}

export { setUserInfo, clearStore, getToken }
