import React, { useState, useEffect, Suspense, lazy } from 'react';
import { Switch, Redirect } from 'react-router-dom';
import PropsRoute from './PropsRoute';
import './App.css';
import { clearStore, setUserInfo } from './helpers/AuthHelper';
import store from 'store';
import BaseLayout from './components/BaseLayout';
import { flatConfig, staticPath } from './RouteConfig';

const Loading = () => <div>Loading...</div>;

const Login = lazy(() => import('./pages/Login'));

export default function App (props) {
  const [isLogin, setIsLogin] = useState(undefined);
  const [user, setUser] = useState({});
  const [hotelId, setHotelId] = useState(undefined);

  useEffect(() => {
    const token = store.get('jwt_token');
    if(token) {
      setIsLogin(true);
      const userInfo = store.get('user_info');
      setUser({...userInfo});
      userInfo.hotelIds[0] && setHotelId(userInfo.hotelIds[0]);
    } else {
      logout();
    }
  }, []);

  const login = (data) => {
    console.log(data);

    setIsLogin(true);
    setUser({username: data.username, hotelIds: data.hotelIds});
    setUserInfo(data.token, {username: data.username, hotelIds: data.hotelIds});
    data.hotelIds[0] && setHotelId(data.hotelIds[0]);
    props.history.push(staticPath.home);
  }

  const logout = () => {
    setIsLogin(false);
    setUser({});
    clearStore();
  }

  const onSelectHotel = (id) => {
    setHotelId(id);
  }
  
  if (typeof isLogin === 'undefined') {
    return <Loading/>;
  } else if (isLogin === false) {
    return (
      <div>
        <Suspense fallback={<Loading/>}>
          <Switch>
            <PropsRoute 
              path={staticPath.login}
              component={Login}
              login={login.bind(this)}
            />

            <Redirect to={staticPath.login} />
          </Switch>
        </Suspense>
      </div>
    );
  } else {
    return (
      <div style={{display: "flex"}}>
        <BaseLayout userInfo={user} 
          hotelId={hotelId} 
          logout={logout.bind(this)} 
          onSelectHotel={onSelectHotel}
          {...props} 
        >
          <Suspense fallback={<Loading/>}>
            <Switch>
              {
                flatConfig.map((item, index) => {
                  return <PropsRoute path={item.path} 
                    component={item.component} 
                    exact={item.exact} 
                    key={index} 
                    userInfo={user}
                    hotelId={hotelId}
                  />
                })
              }

              <Redirect to={staticPath.home} />
            </Switch>
          </Suspense>
        </BaseLayout>
      </div>
    );
  }
  
}
