import React, { useState, useEffect } from "react";
import { Layout, Menu, Icon, Avatar, Dropdown, Breadcrumb } from "antd";
import { NavLink, Link } from 'react-router-dom';
import routes, { staticPath } from '../RouteConfig';
const { Header, Content } = Layout;

export default function (props) {
  const [color, setColor] = useState('#f56a00');

  useEffect(() => {
    const colorList = ['#f56a00', '#7265e6', '#ffbf00', '#00a2ae'];
    const item = colorList[Math.floor(Math.random() * colorList.length)];
    setColor(item);
  }, [])

  // user menu dropdown
  const menu = (
    <Menu>
      <Menu.Item key="0">Profile</Menu.Item>
      <Menu.Item key="1" onClick={() => props.logout()}>Logout</Menu.Item>
    </Menu>
  );

  // hotel menu dropdown
  const hotelIds = (
    <Menu>
      {
        props.userInfo.hotelIds && props.userInfo.hotelIds.map(item => {
          return (
            <Menu.Item key={item} onClick={() => props.onSelectHotel(item)}>{'Khách sạn: ' + item}</Menu.Item>
          )
        })
      }
      <Menu.Divider />
      <Menu.Item key='create' onClick={() => props.history.push(staticPath.hotelCreate)}>
        <span><Icon type='plus' /> Thêm mới khách sạn</span>
      </Menu.Item>
    </Menu>
  )

  // breadcrumb
  const breadcrumbNameMap = {};

  routes.map((item, index) => {
    if (item.path !== '/home') {
      return breadcrumbNameMap[item.path] = {
        title: item.title,
        icon: item.icon
      }
    }
  })

  const { location } = props;
  const pathSnippets = location.pathname.split('/').filter(i => i);
  const extraBreadcrumbItems = pathSnippets.map((_, index) => {
    const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
    if (breadcrumbNameMap[url]) {
      return (
        <Breadcrumb.Item key={url}>
          <Link to={url}>
            <Icon type={breadcrumbNameMap[url].icon} /> {' '}
            {breadcrumbNameMap[url].title}
          </Link>
        </Breadcrumb.Item>
      );
    }
  });
  const breadcrumbItems = [(
    <Breadcrumb.Item key="home">
      <Link to="/"> <Icon type="dashboard" /> {' '} Home</Link>
    </Breadcrumb.Item>
  )].concat(extraBreadcrumbItems);

  const routesHeader = routes.filter(item => !item.hide_on_header || !item.hide_on_header === true)

  // set active menu
  const activeRoute = routesHeader.map((item, index) => {
    if (props.location.pathname.indexOf(item.path) > -1) {
      return item.path;
    }
  })

  return (
    <Layout style={{ minHeight: "100vh" }}>
      <Layout>
        <Header style={{ background: "#fff", padding: 0 }}>
          <Menu
            theme="dark"
            mode="horizontal"
            style={{ lineHeight: '64px' }}
            selectedKeys={[...activeRoute]}
          >
            {
              routesHeader.map((item, index) => {
                return (
                  <Menu.Item key={item.path}>
                    {/* <Icon type={item.icon} /> */}
                    <span>{item.title}</span>
                    <NavLink to={item.path}></NavLink>
                  </Menu.Item>
                )
              })
            }
            <Menu.Item key="user" style={{
              float: 'right', display: 'flex',
              justifyContent: "space-around",
              alignItems: 'center', width: "180px"
            }}>
              <Dropdown overlay={menu} trigger={['click']}>
                <div>
                  {
                    props.userInfo.avatar ?
                      <Avatar src={props.userInfo.avatar} />
                      : <Avatar size='large' style={{ verticalAlign: 'middle', backgroundColor: color }} >
                        {props.userInfo.username ? props.userInfo.username.substring(0, 4) : 'U'}
                      </Avatar>
                  }
                  <span>{' ' + props.userInfo.username}</span>
                </div>
              </Dropdown>
            </Menu.Item>
            <Menu.Item key="hotelId" style={{
              float: 'right', display: 'flex',
              justifyContent: "space-around",
              alignItems: 'center'
            }}>
              <Dropdown overlay={hotelIds} trigger={['click']}>
                <span>{'Khách sạn: ' + props.hotelId}</span>
              </Dropdown>
            </Menu.Item>
          </Menu>

        </Header>
        <Breadcrumb style={{ margin: '10px 16px' }}>
          {breadcrumbItems}
        </Breadcrumb>
        <Content
          style={{
            margin: "10px 16px",
            padding: 24,
            background: "#fff",
            minHeight: 280
          }}
        >
          {props.children}
        </Content>
      </Layout>
    </Layout>
  );
}
