function flatRouteConfig(routes) {
    let flatRoutes = []
    Array.isArray(routes) && routes.forEach(item => {
        flatRoutes.push(item)
        if (item.children) {
            flatRoutes.push(...item.children)
        }
    })
    return flatRoutes
}

export const flatConfig = flatRouteConfig(routes)

export default routes